import * as PIXI from 'pixi.js';

import { Tile } from './Tile';
import { IFramerMap, IFramerState } from '../../common/types/types.framer';
import { IResourceList, IResourceMap } from '../../common/types/types.resource';
import { config } from '../../config';
import { IRendererOpts } from '../../common/types/types.render';

export class RendererComponent {
  public get size() {
    return Math.min(this._size, this._sizeMax);
  }
  private _size = 2000;
  private _sizeMax = this.opts.size || config.tileSize;
  private _lastState?: IFramerState;

  public textureList: IResourceMap<PIXI.Texture> = {};
  private _engine = new PIXI.Application({
    width: window.innerWidth,
    height: window.innerHeight,
    transparent: true,
  });

  constructor(private _container: HTMLElement, private _map: IFramerMap, resourceList: IResourceList, public opts: IRendererOpts = {}) {
    this._container.appendChild(this._engine.view);
    this._loadResource(resourceList);
    this._init();
  }

  public render(state: IFramerState) {
    this._lastState = state;
    this._engine.stage.removeChildren();
    this._engine.stage.addChild(...this._makeStageFromState(state));
  }

  public resize(): void {
    const width = this._map.width * this.size;
    const height = this._map.height * this.size;

    this._engine.renderer.resize(width, height);
    if (this._lastState) this.render(this._lastState);
  }

  public dispose() {
    if (this.opts.autoresize) {
      window.removeEventListener('resize', this._onResize);
    }
  }

  private _init() {
    if (this.opts.autoresize) {
      this._onResize();
      window.addEventListener('resize', this._onResize);
    } else {
      this.resize();
    }
  }

  private _onResize = () => {
    this._size = this._container.offsetWidth / this._map.width;
    this.resize();
  };

  private _makeStageFromState(state: IFramerState) {
    const childList: PIXI.DisplayObject[] = [];
    for (let y = 0; y < state.length; y++) {
      const row = state[y];
      for (let x = 0; x < row.length; x++) {
        const cell = row[x];
        if (!cell) continue;
        childList.push(...new Tile(x, y, cell, this.textureList, this.size).toChilds());
      }
    }
    return childList;
  }

  private _loadResource(list: IResourceList) {
    const background = list.find((r) => r.name === 'background' && r.isLoaded);
    if (background) {
      this._engine.view.style.backgroundImage = `url(${background.url})`;
      this._engine.view.style.backgroundSize = 'cover';
    }

    list.forEach((r) => {
      const texture = new PIXI.Texture(new PIXI.BaseTexture(r.url));
      this.textureList[r.name] = texture;
    });
  }
}
