import { config } from '../../config';
import { IResource, IResourceData, TResourceName, TSkinID } from '../types/types.resource';

export class Resource implements IResource {
  public get url() {
    return `${config.resourcesURL}/${this._data.owner}/${this.skin}/${this._data.name}.${this._data.ext}`;
  }

  public image = new Image();
  public isLoaded = false;
  public isError = false;

  constructor(public name: TResourceName, public skin: TSkinID, private _data: IResourceData) {}

  public load(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (this.isLoaded) return resolve();
      this.image.src = this.url;
      this.image.onload = () => {
        this._onLoad();
        resolve();
      };
      this.image.onerror = () => {
        this._onError();
        reject();
      };
    });
  }

  private _onLoad() {
    this.isLoaded = true;
    this._clearListener();
  }

  private _onError() {
    this.isError = true;
    this._clearListener();
  }

  private _clearListener() {
    this.image.onload = null;
    this.image.onerror = null;
  }
}
