import { IFramerBase, IFramerCell, IFramerDiff, IFramerMap, IFramerPlayer, IFramerStateMap } from '../../../common/types/types.framer';
import { IServerV4Cell, IServerV4Data, IServerV4Diff, IServerV4PayloadCell, IServerV4Player } from './adapter.types.v4';

export class AdapterV4 implements IFramerBase {
  static getVerison(data: IServerV4Data) {
    return data.version;
  }

  static playerAdapter(data: IServerV4Player[]): IFramerPlayer[] {
    return data.map((p) => ({
      Username: p.username,
      Picture: p.avatar,
      Skin: p.skin,
      Span: p.source,
      Version: p.version,
      FC: p.conversion,
      X: p.Point.x,
      Y: p.Point.y,
      Wealth: parseInt(p.wealth),
      Lang: p.lang,
      Stats: {
        Age: p.stats.age,
        Ants: p.stats.ants,
        Hive: p.stats.hive,
        Errors: p.stats.errors,
      },
    }));
  }

  static cellParse(cell: IServerV4Cell): IFramerCell | undefined {
    return cell
      ? {
          event: Math.floor(cell / 1000),
          hive: Math.floor((cell % 1000) / 100),
          food: Math.floor((cell % 100) / 10),
          ant: Math.floor(cell % 10),
        }
      : undefined;
  }

  static diffParse(diff: IServerV4Diff): IFramerDiff {
    return {
      x: diff.L % 100,
      y: Math.floor(diff.L / 100),
      cell: AdapterV4.cellParse(diff.V),
    };
  }

  stateMap: IFramerStateMap;
  playerList: IFramerPlayer[];
  diffMatrix: IFramerDiff[][];
  map: IFramerMap;

  constructor(data: IServerV4Data) {
    this.stateMap = { 0: data.canvas.map((row) => row.map(AdapterV4.cellParse)) };
    this.playerList = AdapterV4.playerAdapter(data.bots);
    this.diffMatrix = data.frames.map((row) => row.map(AdapterV4.diffParse));
    this.map = {
      height: data.map.canvas.height,
      width: data.map.canvas.width,
      skin: data.map.theme,
    };
  }

  cellJSONify(cell?: IFramerCell): IServerV4PayloadCell {
    const r: IServerV4PayloadCell = {};
    if (!cell) return r;
    cell.ant != null && (r.ant = cell.ant.toString());
    cell.hive != null && (r.hive = cell.hive.toString());
    !!cell.food && (r.food = cell.food);
    return r;
  }
}
