import { IServerData } from '../common/types/types.server';
import axios from 'axios';
import JSZip from 'jszip';

export class ServiceData {
  public static async getData(source: string) {
    const data = await this._download(source);
    if (!data) throw new Error(' ');
    return data;
  }

  private static async _download(url: string) {
    const isZip = url.endsWith('.zip');
    const res = await axios.get(url, isZip ? { responseType: 'arraybuffer' } : undefined);
    const data = isZip ? await this._unzip(res.data, url) : res.data;
    return typeof data === 'string' ? this._parseData(data) : (data as IServerData);
  }

  private static _unzip(data: string, pathname: string) {
    const fileName = (pathname.split('/').pop() as string).replace('.zip', '.json');
    return JSZip.loadAsync(data).then((zip) => zip.file(fileName).async('text'));
  }

  private static _parseData(data: string) {
    try {
      const parsedData: IServerData = JSON.parse(data);
      return parsedData;
    } catch (e) {
      return undefined;
    }
  }
}
