import LRUCache from 'lru-cache';

import { IFramerBase, IFramerMap, IFramerPlayer, IFramerSkins, IFramerState, IFramerStateMap, IFramerDiff, IFramer, IFramerCell } from '../common/types/types.framer';
import { EResourceOwner } from '../common/types/types.resource.d';

export class ServiceFramer implements IFramer {
  public playerList: IFramerPlayer[];
  public stateMap: IFramerStateMap;
  public stateCache: LRUCache<number, IFramerState>;
  public diffMatrix: IFramerDiff[][];
  public map: IFramerMap;
  public cellJSONify: (cell?: IFramerCell) => any;

  private _frame = 0;

  public get frame() {
    return this._frame;
  }
  public set frame(index: number) {
    if (index >= this.frameTotal || index < 0) return;
    if (!this._getState(index)) this._calcStateTo(index);
    this._frame = index;
  }

  public get state() {
    return this._getState(this._frame);
  }

  public get frameTotal() {
    return this.diffMatrix.length;
  }
  public get skin(): IFramerSkins {
    return { [EResourceOwner.CLIENT]: Array.from(new Set(this.playerList.map((p) => p.Skin))), [EResourceOwner.SERVER]: this.map.skin };
  }

  private get _stateCacheStep() {
    return Math.max(Math.floor(this.frameTotal / 50), 200);
  }

  constructor(data: IFramerBase) {
    this.playerList = data.playerList;
    this.stateMap = data.stateMap;
    this.diffMatrix = data.diffMatrix;
    this.map = data.map;
    this.cellJSONify = data.cellJSONify;

    this.stateCache = new LRUCache<number, IFramerState>(50);
  }

  private _calcStateTo(index: number) {
    if (index === this._frame + 1) this._calcStateNext(index);
    else
      for (let i = this._getClosestStateIndex(index) + 1; i <= index; i++) {
        this._calcStateNext(i);
      }
  }

  private _calcStateNext(frame: number) {
    const stateClone = this._getState(frame - 1).map((row) => row.slice());
    const diffList = this.diffMatrix[frame];
    diffList.forEach((diff) => (stateClone[diff.y][diff.x] = diff.cell));
    if (frame % this._stateCacheStep === 0) this.stateMap[frame] = stateClone;
    else this.stateCache.set(frame, stateClone);
  }

  private _getClosestStateIndex(index: number) {
    const cacheKeys = this.stateCache.keys();
    const stateKeys = Object.keys(this.stateMap)
      .map((s) => parseInt(s, 10))
      .concat(cacheKeys.length ? Math.max(...cacheKeys) : 0)
      .sort((a, b) => a - b);
    for (let i = 0; i < stateKeys.length; i++) {
      if (index < stateKeys[i]) return stateKeys[i - 1];
    }
    return stateKeys.pop() as number;
  }

  private _getState(index: number) {
    return this.stateMap[index] || this.stateCache.get(index);
  }
}
