import { TServerVersion } from '../common/types/types.server';
import { IAdapterList } from './adapter.types';
import { AdapterV3 } from './server/v3/adapter.v3';
import { AdapterV4 } from './server/v4/adapter.v4';

export const adapterList: IAdapterList = {
  '3.0': AdapterV3,
  '4.0': AdapterV4,
};

export function getVersion(data: any) {
  let version = '';
  Object.values(adapterList).forEach(item => {
    if (!version) version = item.getVerison(data);
  });
  return version as TServerVersion;
}