import { IFramerBase, IFramerCell, IFramerDiff, IFramerMap, IFramerPlayer, IFramerStateMap } from '../../../common/types/types.framer';
import { IServerV3Cell, IServerV3Data, IServerV3Diff, IServerV3PayloadCell, IServerV3Player } from './adapter.types.v3';

export class AdapterV3 implements IFramerBase {
  static getVerison(data: IServerV3Data) {
    return data.Version;
  }

  static playerAdapter(data: IServerV3Player[]): IFramerPlayer[] {
    return data.map((p) => ({
      Username: p.Username,
      Picture: p.Picture,
      Skin: p.Skin,
      Span: p.Span,
      Version: p.Version,
      FC: p.FC,
      X: p.X,
      Y: p.Y,
      Wealth: p.Wealth,
      Lang: p.Lang,
      Stats: {
        Age: p.Stats.Age,
        Ants: p.Stats.Ants,
        Hive: p.Stats.Hive,
        Errors: p.Stats.Errors,
      },
    }));
  }

  static cellParse(cell: IServerV3Cell): IFramerCell | undefined {
    return cell
      ? {
          event: Math.floor(cell / 1000),
          hive: Math.floor((cell % 1000) / 100),
          food: Math.floor((cell % 100) / 10),
          ant: Math.floor(cell % 10),
        }
      : undefined;
  }

  static diffParse(diff: IServerV3Diff): IFramerDiff {
    return {
      x: diff.L % 100,
      y: Math.floor(diff.L / 100),
      cell: AdapterV3.cellParse(diff.V),
    };
  }

  stateMap: IFramerStateMap;
  playerList: IFramerPlayer[];
  diffMatrix: IFramerDiff[][];
  map: IFramerMap;

  constructor(data: IServerV3Data) {
    this.stateMap = { 0: data.Canvas.map((row) => row.map(AdapterV3.cellParse)) };
    this.playerList = AdapterV3.playerAdapter(data.Players);
    this.diffMatrix = data.Frames.map((row) => row.map(AdapterV3.diffParse));
    this.map = {
      height: data.MapSettings.Height,
      width: data.MapSettings.Width,
      skin: data.MapSettings.Skin,
    };
  }

  cellJSONify(cell?: IFramerCell): IServerV3PayloadCell {
    const r: IServerV3PayloadCell = {};
    if (!cell) return r;
    cell.ant != null && (r.ant = cell.ant.toString());
    cell.hive != null && (r.hive = cell.hive.toString());
    !!cell.food && (r.food = cell.food);
    return r;
  }
}
