import { ERenderLayer } from './types.render';

export type TSkinID = number;
export type ILayerResourceMap = { [L in ERenderLayer]: Record<number, IResourceData> };
export type IResourceMap<S extends any> = { [N in TResourceName]?: S };

export type TResourceName =
  | 'birth'
  | 'death'
  | 'noaction'
  | 'slow'
  | 'badmove'
  | 'badload'
  | 'badunload'
  | 'badeat'
  | 'collision'
  | 'error'
  | 'foodsmall'
  | 'foodmid'
  | 'foodbig'
  | 'fog'
  | 'hive'
  | 'ant'
  | 'background';

export enum EResourceCtx {
  USER,
  EVENT,
  TILE,
  FOOD,
}

export enum EResourceOwner {
  SERVER = 'server',
  CLIENT = 'client',
}

export interface IResource {
  name: TResourceName;
  skin: TSkinID;
  image: HTMLImageElement;
  readonly url: string;
  readonly isLoaded: boolean;
  readonly isError: boolean;
  load(): Promise<void>;
}

export interface IResourceData {
  owner: EResourceOwner;
  name: TResourceName;
  ext: string;
}

export type IResourceList = IResource[];
