import { Resource } from '../common/class/class.resource';
import { layerResourceMap } from '../common/const/const.resource';
import { IFramerSkins } from '../common/types/types.framer';
import { EResourceOwner, IResource, IResourceData, IResourceList } from '../common/types/types.resource.d';
import { getResourceName } from '../common/helpers/helper.resource';

export class ServiceResource {
  public list: IResourceList = [];

  constructor(private _skins: IFramerSkins) {
    Object.values(layerResourceMap).forEach((map) => this._fillResourceMap(map));
  }

  public download() {
    return Promise.all(this.list.map((r) => r.load()));
  }

  private _fillResourceMap(map: Record<any, IResourceData>) {
    Object.values(map).forEach((data) => {
      if (data.owner === EResourceOwner.CLIENT) {
        this._skins[data.owner].forEach((s) => {
          this._addResource(new Resource(getResourceName(data.name, s), s, data));
        });
      } else {
        this._addResource(new Resource(getResourceName(data.name), this._skins[data.owner], data));
      }
    });
  }

  private _addResource(resource: IResource) {
    const finded = this.list.find((r) => r.name === resource.name);
    if (!finded) this.list.push(resource);
  }
}
