import './ControlPanel.less';

import { EAppEvent } from '../../common/types/types.event.d';
import { IFramer, IFramerState } from '../../common/types/types.framer';
import { config } from '../../config';

type TContolPanelActionBase = 'prev' | 'play' | 'stop' | 'next' | 'download';

interface IControlBase {
  name: TContolPanelActionBase;
  el?: HTMLElement;
}

interface IControlMove {
  container: HTMLElement;
  current: HTMLElement;
}

type IControlRenderer = (state: IFramerState) => void;
type IControlEmitter = (event: string | symbol, ...args: any[]) => boolean;

const controlBaseList: TContolPanelActionBase[] = ['prev', 'play', 'stop', 'next', 'download'];

export class ControlPanelComponent {
  private _root: HTMLElement = this._createElement('ah-panel');
  private _controlBaseList!: IControlBase[];
  private _controlTrack!: IControlMove;

  private _interval: any;
  private _speed = config.speed;

  public get speed() {
    return this._speed;
  }
  public set speed(num: number) {
    this._speed = num;
    if (this._interval) {
      this._stop(true);
      this._play(true);
      this._emit(EAppEvent.SPEED);
    }
  }

  private get frameTotal() {
    return this._framer.frameTotal;
  }

  private get frame() {
    return this._framer.frame;
  }
  private set frame(index: number) {
    if (index >= this.frameTotal || index < 0) {
      this._stop();
      if (index >= this.frameTotal) this._emit(EAppEvent.END);
      return;
    }
    this._framer.frame = index;
    this._render(this._framer.state);
    this._controlTrack.current.style.width = index / this._percent + '%';
  }

  private get _percent() {
    return this.frameTotal / 100;
  }

  constructor(private _container: HTMLElement, private _framer: IFramer, private _render: IControlRenderer, private _emit: IControlEmitter) {
    this._createBaseControls();
    this._createControlTrack();
    this._createControlSpeed();
    this._container.appendChild(this._root);
  }

  public next = () => {
    this._stop();
    this.frame++;
    this._emit(EAppEvent.NEXT);
  };

  public prev = () => {
    this._stop();
    this.frame--;
    this._emit(EAppEvent.PREV);
  };

  public move = (index: number) => {
    this.frame = index;
    this._emit(EAppEvent.MOVE);
  };

  public play = () => this._play();
  public stop = () => this._stop();
  public download = () => this._frameCurrentDownload();

  private _play(noEmit?: boolean) {
    if (this._interval) return;
    this._interval = window.setInterval(() => this.frame++, config.interval / this._speed);
    this._root.classList.add('playing');
    if (!noEmit) this._emit(EAppEvent.PLAY);
  }

  private _stop(noEmit?: boolean) {
    if (!this._interval) return;
    clearInterval(this._interval);
    this._interval = undefined;
    this._root.classList.remove('playing');
    if (!noEmit) this._emit(EAppEvent.STOP);
  }

  private _rewindLine = (event: MouseEvent) => {
    const rec = (this._controlTrack.container.getBoundingClientRect() as any) as DOMRect;
    const currentFrame = parseInt((this.frameTotal * (((event.pageX - rec.x) * (100 / rec.width)) / 100)).toString());
    this.move(currentFrame);
  };

  private listnerDispose = () => {
    document.body.removeEventListener('selectstart', this._eventCancel);
    document.body.removeEventListener('mousemove', this._rewindLine);
    document.body.removeEventListener('mouseup', this.listnerDispose);
    document.body.removeEventListener('mouseleave', this.listnerDispose);
  };

  private _createControlTrack() {
    this._controlTrack = {
      container: this._createElement('ah-track'),
      current: this._createElement('ah-track_current'),
    };

    this._controlTrack.container.appendChild(this._controlTrack.current);
    this._root.appendChild(this._controlTrack.container);

    this._controlTrack.container.addEventListener('mousedown', (event) => {
      this._rewindLine(event);
      document.body.addEventListener('selectstart', this._eventCancel);
      document.body.addEventListener('mousemove', this._rewindLine);
      document.body.addEventListener('mouseup', this.listnerDispose);
      document.body.addEventListener('mouseleave', this.listnerDispose);
    });
  }

  private _createControlSpeed() {
    const baseList = this._createElement('ah-panel_base');
    config.speedList.forEach((speed) => {
      const el = this._createElement(`ah-control speed`, speed.toString());
      el.addEventListener('click', () => (this.speed = speed));
      baseList.appendChild(el);
    });
    this._root.appendChild(baseList);
  }

  private _createBaseControls() {
    const baseList = this._createElement('ah-panel_base');
    this._controlBaseList = controlBaseList.map((name) => {
      const el = this._createElement(`ah-control ${name}`);
      el.addEventListener('click', this[name]);
      baseList.appendChild(el);
      return { name, el };
    });
    this._root.appendChild(baseList);
  }

  private _createElement(className: string, html?: string) {
    const element = document.createElement('div');
    element.className = className;
    if (html) element.innerHTML = html;
    return element;
  }

  private _eventCancel() {
    return false;
  }

  private _frameCurrentDownload() {
    const a = document.createElement('a');
    const data = JSON.stringify(this._framer.state.map(y => y.map(x => this._framer.cellJSONify(x))));
    a.href = URL.createObjectURL( new Blob([data], { type: 'application/json' }) );
    a.download = `frame-${this._framer.frame}`;
    a.click();
  }
}
