import * as PIXI from 'pixi.js';

import { foodSizeMap } from '../../common/const/const.render';
import { resourceEventMap, resourceFoodMap } from '../../common/const/const.resource';
import { getResourceName } from '../../common/helpers/helper.resource';
import { IFramerCell } from '../../common/types/types.framer';
import { ELayerEvent, ELayerGround } from '../../common/types/types.render.d';
import { IResourceMap, TResourceName } from '../../common/types/types.resource';

const NOT_GROUND_EVENT = [ELayerEvent.BIRTH, ELayerEvent.DEATH];

export class Tile {
  private get x() {
    return this.tileX * this._size;
  }
  private get y() {
    return this.tileY * this._size;
  }

  constructor(public tileX: number, public tileY: number, public cell: IFramerCell, private _textureList: IResourceMap<PIXI.Texture>, private _size: number) {}

  public toChilds(): PIXI.DisplayObject[] {
    const arr: PIXI.DisplayObject[] = [];
    if (this._ground) arr.push(this._ground);
    if (this._ant) arr.push(this._ant);
    if (this._food) arr.push(this._food);
    return arr;
  }

  private get _ground() {
    if (this.cell.hive === 0) return;

    let name = getResourceName('hive', this.cell.hive);
    if (this.cell.hive === ELayerGround.FOG) name = getResourceName('fog');
    else if (this.cell.event && !NOT_GROUND_EVENT.includes(this.cell.event)) name = getResourceName(resourceEventMap[this.cell.event].name, this.cell.ant);
    return this._makeSprite(name);
  }

  private get _ant() {
    if (!this.cell.ant && this.cell.event !== ELayerEvent.BIRTH && this.cell.event !== ELayerEvent.DEATH) return;

    let name = getResourceName('ant', this.cell.ant);
    if (this.cell.event === ELayerEvent.BIRTH) name = getResourceName('birth', this.cell.ant);
    else if (this.cell.event === ELayerEvent.DEATH) name = getResourceName('death', this.cell.ant);

    if (NOT_GROUND_EVENT.includes(this.cell.event || -1)) {
      return this._makeSprite(name);
    } else {
      const texture = this._textureList[name];
      if (!texture) return;
      const originalRatio = texture.width / texture.height;
      const sprite = new PIXI.Sprite(texture);
      sprite.width = this._size * 1.1;
      sprite.height = Math.floor(sprite.width / originalRatio);
      sprite.position.set(Math.round(this.x - (sprite.width - this._size) / 2), Math.round(this.y - (sprite.height - this._size) / 2));
      return sprite;
    }
  }

  private get _food() {
    if (!this.cell.food) return;
    const name = getResourceName(resourceFoodMap[foodSizeMap[this.cell.food]].name);
    return this._makeSprite(name);
  }

  private _makeSprite(name: TResourceName) {
    const sprite = new PIXI.Sprite(this._textureList[name]);
    // if (!this._textureList[name]) debugger;

    sprite.width = this._size;
    sprite.height = this._size;
    sprite.position.set(this.x, this.y);
    return sprite;
  }
}
